import React, { PropTypes } from 'react';
import { ProgressBar as ProgressBarContainer, Progress, ProgressDot, ProgressText }
  from '../styledComponents';

const steps = [
  { id: 1, name: 'Vêtements' },
  { id: 2, name: 'Retouches' },
  { id: 3, name: 'RDV' },
  { id: 4, name: 'Coordonnées' },
];

const ProgressBar = ({ step }) => (
  <ProgressBarContainer>
    {steps.map(stepIt =>
      <Progress key={stepIt.id} isActive={step === stepIt.id}>
        <ProgressDot>
          {stepIt.id}
        </ProgressDot>
        <ProgressText>
          {stepIt.name}
        </ProgressText>
      </Progress>,
    )}
  </ProgressBarContainer>
);

ProgressBar.propTypes = {
  step: PropTypes.number.isRequired,
};

export default ProgressBar;



